 let ck2_mixers = []
 let ck2_models = new Array(ck2_Paths.length) //we need to create an array with the same lengths as ck2_Paths but undefined contents, to be populated with the models after load
 
 var ck2_loader = new GLTFLoader()
 ck2_loader.setDRACOLoader(dracoLoader)
 
 for (let pathNum = 0; pathNum < ck2_Paths.length; pathNum++)
 {
    ck2_loader.load(
         ck2_Paths[pathNum],
         (gltf) =>
             {
                ck2_models[pathNum] = gltf.scene
                 //scene.add(ck2_models[pathNum])
                 ck2_models[pathNum].position.x = 0.5
                 ck2loadMixers(ck2_models[pathNum], gltf.animations) //push the model & animations info to loadMixers function so we can control the actions
             },
         undefined, (error) => {console.log(error)}
     )
 }
 
 const ck2loadMixers = (model, animations) =>
 {
     let mixer = new THREE.AnimationMixer(model)
     const numAnimations = animations.length;
 
     for ( let i = 0; i < numAnimations; i++ )
     {
         let action = mixer.clipAction(animations[i])
         action.setLoop(THREE.LoopOnce)
         
         const duration = 8;
         action.startAt(i*duration); //so action 0 starts at 0, 1 starts at 1*duration (eg 12), 2 starts at 2*duration (eg 24)
         action.setDuration(duration)
         action.play()
             
         if (i == numAnimations-1) //we only need to clamp the last action
         {
             action.clampWhenFinished = true 
         }
     }
     ck2_mixers.push(mixer)
 }
 
 // Add characters' labels on top of their model
 const ck2labelTexts = ['Galuh Ajeng,\nLiku\'s daughter','Paduka Liku,\nsecond consort','Mahadewi,\nfirst consort','King of Daha','Queen of Daha','Candra Kirana,\nQueen\'s daughter']
 const ck2lTimes = [0,8,12,28,32]
 const ck2lOpacity = [0,0,1,1,0]
 
 let ck2labels = []
 let ck2_labelMixers = []
 
 for (let i in ck2labelTexts)
 {
     let posX = -1.2+(i*0.4) // each label is 0.4 apart from each other on X axis, starting from -1.2
     const labelText = new Text()
     //scene.add(labelText)
     labelText.position.set(posX,0.85,0)
     labelText.text = ck2labelTexts[i]
     labelText.fontSize = 0.02
     labelText.color = 'black'
     labelText.textAlign = 'center'
     labelText.sync()
     ck2labels.push(labelText)
 
     // Animate the opacity of the labels (to hide them when characters move)
     
     const lOpacityKF = new NumberKeyframeTrack('.fillOpacity', ck2lTimes, ck2lOpacity)
     const labelClip = new AnimationClip('labelOpacity', -1, [lOpacityKF])
     const labelMixer = new AnimationMixer(labelText)
     const labelAction = labelMixer.clipAction(labelClip)
     labelAction.play().setLoop(THREE.LoopOnce)
     labelAction.clampWhenFinished = true 
 
     ck2_labelMixers.push(labelMixer)
 }


