//nText.text = - to be controlled with animation -

const text1 = 'Our story begins in Daha, where there lived a princess most beautiful, named Candra Kirana.\n\n\nShe was betrothed to the Prince of Kuripan, Raden Inu.'
const text2 = 'The palace of Daha is rife with jealousy as the King has more than one wife.\n\nIn an attempt to turn the Kings attention to herself, Paduka Liku, the Kings consort, conceives an evil plot.\n\nThe plot involves the poisoning of Candra Kiranas mother, the Queen of Daha.'
const text3 = 'To help her cope with the loss of her mother, Raden Inu makes two dolls to be sent to Daha.\n\nOne is made of silver and adorned with beautiful clothes and the other, made of gold and wrapped with tattered cloth and a black ribbon.'
const text4 = 'Despite getting the lesser of the 2 gifts, Candra Kirana is very happy as she finds a beautiful golden doll, relieving her of her loneliness after her mothers passing.\n\nOnce word of Candra Kirana having the better gift reaches her half-sister, Candra Kirana is asked to give the doll away.'
const text5 = 'Not wanting to lose her only companion, Candra Kirana refuses to yield, resulting in the King cutting her hair as punishment.\n\nDistraught, Candra Kirana runs away from Daha to establish her own city, disguised as a man called\n\nPanji Semirang.'
const text6 = 'This is where our stories converge with Raden Inu coming into the city.\n\nRaden Inu falls in love with Panji Semirang and the latter feels the exact same way.\n\nCandra Kirana, however, does not want to reveal her true identity.'
const text7 = 'Reluctantly, she lets Raden Inu continue on his journey to Daha to be wed, knowing that it will no longer be to her but to her half-sister instead.\n\nDistraught, she leaves the city she had painstakingly built and wanders around Java as a gambuh under a new name, Gambuh Warga Asmara.'
const text8 = 'Upon arriving at Daha, Raden Inu finds Candra Kirana missing.\n\nHe is to wed her half-sister, Galuh Ajeng, instead.\n\nAlas, Raden Inu finds that he could not love Galuh Ajeng and often wonders if Panji Semirang was indeed Candra Kirana.'
const text9 = 'Once again, he sets off from Daha, on a journey to find Panji Semirang...\n\nHowever, the city where he last saw Panji Semirang was empty, void of all people but a lone crying woman, Mahadewi.'
const text10 = 'Inconsolable, Mahadewi mentions being left behind by her daughter Panji Semirang who was also known as Candra Kirana.\n\nRaden Inu falls to his feet in anguish, thinking of the missed opportunities thus far.\n\nHe makes a vow to find Candra Kirana and takes up a pseudonym, Raden Panji Jayeng Kesuma.'
nText.position.set(0,0.65,0)
