/**
 * Viewport
 */
 const sizes = {
    width: window.innerWidth,
    height: window.innerHeight

}


/**
 * Camera
 */
// Base camera
const camFactor = 1500;
const camera = new THREE.OrthographicCamera( -sizes.width / camFactor, sizes.width / camFactor, sizes.height / camFactor, - sizes.height / camFactor, 0.1, 100)
camera.position.set(0,0.5,4)
scene.add(camera)


// Controls -- enabled for dev and testings, disabled for 360imx build

const controls = new OrbitControls(camera, canvas)
controls.target.set(0,0.5,0)
controls.enableDamping = true


/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    alpha: true
})
renderer.shadowMap.enabled = true
renderer.shadowMap.type = THREE.PCFSoftShadowMap
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
renderer.setClearColor( 0x000000, 0 ); 

var igloo = new IglooRenderer(camera, renderer, scene)


window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

    igloo.update()
})

/**
 * Animate
 */
const clock = new THREE.Clock()
let previousTime = 0

const timeText = document.getElementById('eltime')

const tick = () =>
{
    const elapsedTime = clock.getElapsedTime()
    const deltaTime = elapsedTime - previousTime
    previousTime = elapsedTime
    
    timeText.innerHTML = Math.round(elapsedTime)

    scene.add(iText)

    if (textMixer)
    {
        textMixer.update(deltaTime) 
    }
    

    if (elapsedTime > 3) {   
        scene.remove(iText)
        iText.dispose()
        scene.add(movieScreen)
        dahaVideo.play()
    }
    if (elapsedTime > 6)
    {
        dahaVideo.pause()
        scene.add(ckImg)
        if (ckImgMixer)
        {
            ckImgMixer.update(deltaTime)
        }
    }
    if (elapsedTime > 11)
    {
        scene.add(inuImg)
        if (inuImgMixer)
        {
            inuImgMixer.update(deltaTime)
        }
    }
    if (elapsedTime > 17)
    {
        scene.remove(movieScreen, ckImg, inuImg)

        for (let model of ck2_models) {
            scene.add(model)
        }
        for (let label of ck2labels) {
            scene.add(label)
        }
        for (let mixer of ck2_mixers) {
             mixer.update(deltaTime)
        }
        for (let lm of ck2_labelMixers) {
            lm.update(deltaTime)
        }
        nText.position.x = -3
    }

    if (elapsedTime > 43)
    {
        for (let model of ck2_models) {
            scene.remove(model)
        }
        for (let label of ck2labels) {
            scene.remove(label)
            label.dispose()
        }
        for (let model of ri2_models) {
            scene.add(model)
        }
        for (let mixer of ri2_mixers) {
            mixer.update(deltaTime)
        }
        scene.add(ri2labelText) 
        nText.position.x = -1
    }

    if (elapsedTime > 58)
    {
        for (let model of ri2_models) {
            scene.remove(model)
        }
        scene.remove(ri2labelText)
        ri2labelText.dispose()

        for (let model of ck4_models) {
            scene.add(model)
        }
        for (let mixer of ck4_mixers) {
            mixer.update(deltaTime)
        }
        nText.position.x = -3
    }

    if (elapsedTime > 100)
    {
        nText.position.x = -2
    }

    if (elapsedTime > 106)
    {
        for (let model of ck4_models) {
            scene.remove(model)
        }
        for (let model of ck5_models) {
            scene.add(model)
        }
        for (let label of ck5labels) {
            scene.add(label)
            label.dispose()
        }
        for (let mixer of ck5_mixers) {
            mixer.update(deltaTime)
        }
        for (let lm of ck5labelMixers) {
            lm.update(deltaTime)
        }
        
    }

    if (elapsedTime > 143)
    {
        scene.add(gLabel)
    }

    if (elapsedTime > 145)
    {
        for (let model of ck5_models) {
            scene.remove(model)
        }
        for (let label of ck5labels) {
            scene.remove(label)
            label.dispose()
        }
        scene.remove(gLabel)
        gLabel.dispose()
        for (let model of ri4_models) {
            scene.add(model)
        }
        for (let label of ri4_labels) {
            scene.add(label)
        }
        for (let mixer of ri4_mixers) {
             mixer.update(deltaTime)
        }
        for (let lm of ri4_labelMixers) {
            lm.update(deltaTime)
        }   
        nText.position.x = -3
    }

    if (elapsedTime > 177)
    {   
        scene.add(mdLabel)
    }

    if (elapsedTime > 190)
    {
        scene.remove(mdLabel)
        mdLabel.dispose()
        for (let label of ri4_labels) {
            scene.remove(label)
            label.dispose()
        }
    }

    if (elapsedTime > 203)
    {
        for (let model of ri4_models) {
            scene.remove(model)
        }
        scene.remove(nText)
        nText.dispose()
        
        scene.add(epilogue)
        if (epilogueMixer) {
            epilogueMixer.update(deltaTime)
        }
    }

    //autoplay to the next scene after last animation ends
    if (elapsedTime > 225)
    {
        location.replace("https://panji360.github.io/gg_02/index.html?igloo=1")
    }
    
    
    
    
    /*

    if (elapsedTime > 60)
    {
        scene.add(iText)
        //document.addEventListener( 'keydown', onKeyDown )
    }
     */

    

    // Update controls -- only for dev, it should be disabled for 360imx build
    controls.update()

    // Render
    //renderer.render(scene, camera)

    if (igloo.render() != true) {
        renderer.render( scene, camera )
    }


    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()
